.grid-container {
  display: grid;
  grid-template-columns: auto auto auto auto auto;
  padding: 2px;
  width: 450px;
  height: 450px;
  margin-left: 37%;
  margin-top: 5%;
  border: 1px solid orangered;
  box-shadow: 0px 9px 11px 0px black;
}
.grid-item {
  background-color: orangered;
  border: 1px solid white;
  padding: 20px;
  font-size: 30px;
  text-align: center;
  color: white;
}
@media (max-width: 768px) {
  .grid-container {
    display: grid;
    grid-template-columns: 80px 80px 80px 80px 80px;
    padding: 2px;
    width: 400px;
    height: 400px;
    margin-left: 12%;
    margin-top: 5%;
    border: 1px solid orangered;
    box-shadow: 0px 9px 11px 0px black;
  }
  .grid-item {
    background-color: orangered;
    border: 1px solid white;
    padding: 1.5rem 1rem;
    font-size: 22px;
    text-align: center;
    color: white;
    font-weight: 600;
  }
}
